import { Box, Stack, Typography } from "@mui/material";
import React, { useState } from "react";
import { useTranslation } from 'react-i18next';
import "./auth.scss";
import Input from "../../components/input";
import { Email, Password, Person } from "@mui/icons-material";
import Button from "../../components/button";
import cx from "classnames";
// import { useNavigate } from "react-router-dom";
import useAuth from "../../store/auth";
import Forgot from "./Forgot";
import loginIcon from '../../assets/png/loginLogo.png'


const AuthenticationPage = () => {
  const { t, i18n } = useTranslation();
  const [tab, setTab] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [data, setData] = useState({
    fullname: "",
    email: "",
    password: "",
    secret_key: "",
    username: "",
    name:""
  });

  const { login, register } = useAuth();
  const onChange = (e) => {
    if (!e.target) return;
    setData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleAuth = () => {
    const { email, password, fullname, secret_key, username } = data;
    if (tab === 0) {
      login({ username, password });
    }
    if (tab === 1) {
      register({ email, password, fullname, secret_key, username });
    }
  };

  return (
    <Box className="auth-layout">
      <Box className="auth-card">
        <Stack style={{height:"80vh",width:"100%"}} className="flex items-center justify-center">
          <img
            src={loginIcon}
            alt="auth"
          />
        </Stack>
        <Stack style={{height:"81vh",width:'101%'}} className="bg-primary flex flex-col items-center  c-white">
          <h2 className="text-center">WELCOME TO TRACEABILITY</h2>
          <div className="flex items-center gap-20">
            {["LOGIN", "SIGNUP"].map((d, i) => (
              <h3
                onClick={() => setTab(i)}
                className={cx(
                  "c-pointer",
                  tab === i && "border-b",
                  tab === i ? "fs-26" : "fs-18"
                )}
                key={d}
              >
                {d}
              </h3>
            ))}
          </div>
        <Stack sx={{width:'90%',alignItems:'center'}} className="signupScroll">
           <Input
                required
                title={t("username")}
                name="username"
                height="40px"
                placeholder="Enter your username"
                icon={<Person fontSize="small" />}
                onChange={onChange}
                value={data.username}
                width={'86%'}
                iconLeft
                // classes={"inputWidth"}
              />
         
         {tab === 1 &&(
          <>
           <Input
           title={t('fullname')}
           name="fullname"
           height="40px"
           placeholder="Enter your fullname"
           icon={<Person fontSize="small" />}
           onChange={onChange}
           value={data.fullname}
           width={'86%'}
           iconLeft
           // classes={"inputWidth"}
         />
         <Input
         title={t('email')}
         name="email"
         height="40px"
         placeholder="Please enter your username"
         iconLeft
         icon={<Email fontSize="small" />}
         onChange={onChange}
         value={data.email}
         width={'86%'}
       />
       </>
         )}
          <Input
            required
            title={t('password')}
            name="password"
            height="40px"
            placeholder="Please enter your password"
            type="password"
            iconLeft
            icon={<Password fontSize="small" />}
            onChange={onChange}
            value={data.password}
            width={'86%'}
          />
          {tab === 0 && (
          <Stack sx={{alignContent:'flex-start',width:'100%',mt:1}}>
            <Typography sx={{textDecoration:'underline',cursor:'pointer'}} onClick={()=>setModalOpen('forgot')}>Forgot Password ?</Typography>
          </Stack>
          )}
          {tab === 1 && (
            <>
            <Input
              required
              title="Secret Key"
              name="secret_key"
              height="40px"
              placeholder="Please enter tenant's secret code"
              iconLeft
              icon={<Person fontSize="small" />}
              onChange={onChange}
              value={data.secret_key}
              width={'86%'}
            />
            </>
          )}
          <Button
            color="orange"
            onClick={handleAuth}
            classes="mt-5 mb-5 fw-600 uppercase"
          >
            {t('submit')}
          </Button>
          </Stack>
        </Stack>
      </Box>
      <Forgot
        open={modalOpen === "forgot"}
        onClose={() =>{ setModalOpen(false)}}
        data={data}
        setData={setData}
        // onSubmit={handleAdd}
      />
    </Box>
  );
};

export default AuthenticationPage;
